<template>
    <!--<el-dialog title="添加店铺类别" :visible.sync="dialogFormVisible">-->
    <el-dialog title="添加店铺类别" :visible="dialogFormVisible" @update:visible="changeDialogFormVisible">
        <el-form  ref="form"  :model="form">
            <el-form-item prop="shopTypeName" label="类别名称" :label-width="formLabelWidth">
                <el-input v-model="form.shopTypeName" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="类别图片" prop="shopTypePic" :label-width="formLabelWidth">
                <el-upload
                        class="avatar-uploader"
                        name="shopTypePic"
                        action="/ele/addShopType"
                        :data="form"
                        ref="shopTypePic"
                        :on-change="changePic"
                        :auto-upload="false"
                        :headers="{authorization:$store.state.admin.token}"
                        :show-file-list="false"
                        :on-success="handleAvatarSuccess"
                >
                    <img v-if="imageUrl" :src="imageUrl" class="avatar">
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
                </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="changeDialogFormVisible(false)">取 消</el-button>
            <el-button type="primary" @click="addShopType">添 加</el-button>
        </div>
    </el-dialog>
</template>

<script>
    export default {
        name: "add-shop-type",
        props:["dialogFormVisible"],
        data(){
            return {
                imageUrl:"",
                // dialogFormVisible:false,
                formLabelWidth: '120px',
                form:{
                    shopTypeName:""
                }
            }
        },
        mounted(){
            // console.log(,this.dialogFormVisible)
            // console.log(11111,this.$route);
        },
        methods:{
            changeDialogFormVisible(dialogFormVisible){
                this.$emit("update:dialogFormVisible",dialogFormVisible)
                this.imageUrl = "";
            },
            addShopType(){
                this.$refs.shopTypePic.submit()
            },
            changePic(file){
                this.imageUrl = URL.createObjectURL(file.raw);
            },
            handleAvatarSuccess(res, file) {
                // console.log(555,this.$route);
                if(this.$route.name === "shopTypeList"){
                    // console.log(44444,this.$router);
                    this.$store.dispatch("getShopTypeList")
                }
                else{
                    // console.log(333333,this.$router);
                    this.$router.push("/shopTypeList")
                }
                this.imageUrl = "";
                this.$refs.shopTypePic.clearFiles();
                this.$refs.form.resetFields();
                this.changeDialogFormVisible(false)
                // this.dialogFormVisible = false;
            },
        }
    }
</script>

